.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#elemento_soma{

height: 380px;
border: 1px solid;
background-color: #fff6f6;
padding-top: 50px;

}
input{
margin-left: 20px;
width: 50%;
margin-left: 24%;
height:  43px;
border-radius: 4%;
border-radius: 10px;
background-color: #D9D9D9;
margin-bottom: 5px;
font-size: 30px;
box-shadow: 1px 3px 2px rgba(0, 0, 0, 0.25);

}
button{
width: 100%;
background-color: white;
height: 41px;
offset: 0px 4px;
